<template>

  <div>
    <h3>消息信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col>
        <Form :model="formData" :label-width="80">
          <FormItem style="max-width: 500px" label="标题">
            <Input v-model="formData.title" placeholder="请填写标题"></Input>
          </FormItem>
          <FormItem style="max-width: 500px" label="状态">
            <Select v-model="formData.status">
              <Option label="正常" :value="1"></Option>
              <Option label="禁用" :value="0"></Option>
            </Select>
          </FormItem>
          <FormItem>
            <rich-view v-model="formData.rich_text"></rich-view>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>
        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {},
      load: false
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
  },
  methods: {
    loadData () {
      this.load = true
      api.detail(
        { id: this.$route.query.id },
        res => {
          this.load = false
          this.formData = res.data.data
        }, () => {
          this.load = false
        })
    },
    submit () {
      this.load = true
      api.save(this.formData, () => {
        this.load = false
        this.$router.push({ name: 'information' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

